* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: "Cabin", Arial, sans-serif; }

.c-footer {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 672px;
  padding: 80px 120px;
  background: url(../../../../../images/ui-kit/footers/footer7/back.jpg) no-repeat;
  background-size: cover; }
  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .c-footer {
      padding: 40px; } }
  @media only screen and (min-width: 426px) and (max-width: 768px) {
    .c-footer {
      flex-flow: wrap;
      justify-content: center;
      height: 1250px;
      padding: 40px; } }
  @media only screen and (max-width: 425px) {
    .c-footer {
      flex-flow: wrap;
      justify-content: center;
      height: 1850px;
      padding: 20px; } }
  .c-footer__logo {
    width: 150px; }
  .c-footer__menu-item {
    width: 150px; }
    @media only screen and (min-width: 426px) and (max-width: 768px) {
      .c-footer__menu-item {
        width: 200px;
        margin-bottom: 20px; } }
  .c-footer__menu-subitem {
    display: block;
    color: #9d9d9e;
    font-size: 16px;
    line-height: 2;
    letter-spacing: 0.02rem;
    text-decoration: none; }
    .c-footer__menu-subitem:first-child {
      color: #fff;
      line-height: 2.25;
      letter-spacing: 0.05rem;
      text-transform: uppercase;
      pointer-events: none; }
    .c-footer__menu-subitem:hover {
      text-decoration: underline; }
    .c-footer__menu-subitem.new {
      position: relative; }
      .c-footer__menu-subitem.new::after {
        content: 'NEW';
        position: absolute;
        width: 52px;
        height: 28px;
        margin-left: 10px;
        border: 1px solid transparent;
        border-radius: 6px;
        background-color: #d9c26c;
        color: #383737;
        font-size: 14px;
        letter-spacing: 0.05rem;
        text-align: center; }
      .c-footer__menu-subitem.new:hover::after {
        background-color: #c9b248; }
  .c-footer__other {
    width: 260px; }
    @media only screen and (min-width: 426px) and (max-width: 768px) {
      .c-footer__other {
        width: 360px;
        text-align: center; } }
  .c-footer__social-title {
    display: block;
    margin-bottom: 10px;
    color: #fff;
    font-size: 16px;
    line-height: 2.25;
    letter-spacing: 0.05rem;
    text-transform: uppercase; }
  .c-footer__social-item {
    padding: 9px;
    border: 1px solid;
    border-radius: 6px;
    color: #fff;
    font-size: 14px;
    text-decoration: none; }
    @media only screen and (min-width: 769px) and (max-width: 1024px) {
      .c-footer__social-item {
        margin-bottom: 10px; } }
    .c-footer__social-item:not(:last-child) {
      margin-right: 10px; }
    .c-footer__social-item.c--facebook {
      border-color: #3162a2;
      background-color: #3162a2; }
      .c-footer__social-item.c--facebook:hover {
        border-color: #235493;
        background-color: #235493;
        cursor: pointer; }
    .c-footer__social-item.c--twitter {
      border-color: #2eb1eb;
      background-color: #2eb1eb; }
      .c-footer__social-item.c--twitter:hover {
        border-color: #209dd4;
        background-color: #209dd4;
        cursor: pointer; }
    .c-footer__social-item.c--dribbble {
      border-color: #f552b6;
      background-color: #f552b6; }
      .c-footer__social-item.c--dribbble:hover {
        border-color: #d43b99;
        background-color: #d43b99;
        cursor: pointer; }
    .c-footer__social-item.c--vimeo {
      border-color: #3498b9;
      background-color: #3498b9; }
      .c-footer__social-item.c--vimeo:hover {
        border-color: #2485a5;
        background-color: #2485a5;
        cursor: pointer; }
    .c-footer__social-item.c--google-plus {
      border-color: #f25c58;
      background-color: #f25c58; }
      .c-footer__social-item.c--google-plus:hover {
        border-color: #d24440;
        background-color: #d24440;
        cursor: pointer; }
  .c-footer__subscribe {
    margin-top: 50px;
    margin-bottom: 40px; }
    @media only screen and (min-width: 769px) and (max-width: 1024px) {
      .c-footer__subscribe {
        margin-top: 20px; } }
    .c-footer__subscribe-title {
      display: block;
      color: #fff;
      font-size: 16px;
      line-height: 2.25;
      letter-spacing: 0.05rem;
      text-transform: uppercase; }
    .c-footer__subscribe-text {
      display: block;
      margin-bottom: 15px;
