      width: 202px;
      height: 36px;
      margin-right: 10px; }
    .c-footer__subscribe-input {
      width: 100%;
      height: 100%;
      padding: 10px;
      border: 1px solid transparent;
      border-radius: 6px;
      background-color: rgba(255, 255, 255, 0.25);
      outline: none;
      color: #fff;
      font-size: 14px;
      line-height: 1.71;
      letter-spacing: 0.02rem; }
      .c-footer__subscribe-input::placeholder {
