    .c-footer__subscribe-submit-wrapper {
      position: relative;
      display: inline-block;
      width: 36px;
      height: 36px;
      vertical-align: bottom; }
      .c-footer__subscribe-submit-wrapper::before {
        content: '';
        position: absolute;
        top: 50%;
        left: calc(50% - 1px);
        width: 5px;
        height: 5px;
        border-top: solid 2px #fff;
        border-right: solid 2px #fff;
        transform: translate(-50%, -50%) rotate(45deg);
        transform-origin: center; }
    .c-footer__subscribe-submit {
      width: 100%;
      height: 100%;
      border: 1px solid transparent;
      border-radius: 6px;
      background-color: #24aa98;
      outline: none; }
      .c-footer__subscribe-submit:hover {
